import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';

import { ContactPageComponent } from './components/contact-page/contact-page.component';
import { ContactPageResolver } from './resolvers/contact-page.resolver';

export const miscContactRoutes = [
  {
    path: '',
    component: ContactPageComponent,
    resolve: {
      data: ContactPageResolver
    }
  }
];

@NgModule({
  declarations: [
    ContactPageComponent
  ],
  imports: [
    RouterModule.forChild(miscContactRoutes),
    CommonModule
  ],
  providers: [
    ContactPageResolver
  ]
})
export class MiscContactModule { }
